export const VerifySub = (subs, instLinker) => {
  let subTimes = subs
    .filter((sub) => sub.softwareId == parseInt(instLinker / 1000))
    .reduce((accumulator, current) => {
      let Exists = accumulator.some((obj) => obj.code == current.code);
      if (!Exists) {
        accumulator.push(current);
      }
      return accumulator;
    }, [])
    .map((sub, index) => {
      let softwareId = parseInt(instLinker / 1000);

      let subTime = 0;

      if (softwareId == sub.softwareId) {
        let amt = sub.code / (index + 2) - softwareId;

        subTime =
          (amt === 99 && index === 0) || (amt === 29 && index > 0)
            ? 366 * 24 * 60 * 60
            : 0;
      }

      return subTime;
    });

  return (
    subTimes.reduce((a, b) => +a + +b, 0) +
    parseInt(instLinker / 1000) +
    90 * 24 * 60 * 60 -
    parseInt(Date.now() / 1000)
  );
};
