import React from "react";
import {
  CashStack,
  CreditCard2Front,
  Diagram3,
  CashCoin,
  PeopleFill,
  PersonBadge,
} from "react-bootstrap-icons";
import { Dropdown } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
const AdminDash = () => {
  const navigate = useNavigate();
  return (
    <>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`staffs`);
          }}
        >
          <div className="d-flex justify-content-center">
            <PersonBadge className="dash-icon" />
          </div>
          <p className="text-center dash-text">Staffs</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`modes`);
          }}
        >
          <div className="d-flex justify-content-center">
            <CashStack className="dash-icon" />
          </div>
          <p className="text-center dash-text">Payment Modes</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`branches`);
          }}
        >
          <div className="d-flex justify-content-center">
            <Diagram3 className="dash-icon" />
          </div>
          <p className="text-center dash-text">Branches</p>
        </div>
      </div>{" "}
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`groups`);
          }}
        >
          <div className="d-flex justify-content-center">
            <PeopleFill className="dash-icon" />
          </div>
          <p className="text-center dash-text">Groups</p>
        </div>
      </div>{" "}
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`accounts`);
          }}
        >
          <div className="d-flex justify-content-center">
            <CreditCard2Front className="dash-icon" />
          </div>
          <p className="text-center dash-text">Accounts</p>
        </div>
      </div>{" "}
      <div className="col-6 col-md-4 ">
        {" "}
        <div className="dashboard mb-2">
          <Dropdown style={{ width: "100%" }}>
            <Dropdown.Toggle
              style={{
                width: "100%",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
              variant="transparent"
            >
              <div>
                <div className="d-flex justify-content-center">
                  <CashCoin className="dash-icon" />
                </div>
                <p className="text-center dash-text">Expenses</p>
              </div>
            </Dropdown.Toggle>
            <Dropdown.Menu>
              <Dropdown.Item>
                <p
                  className="btn-link"
                  onClick={() => {
                    navigate(`expensecats`);
                  }}
                >
                  Expense Categories
                </p>
              </Dropdown.Item>

              <Dropdown.Item>
                <p
                  className="btn-link"
                  onClick={() => {
                    navigate(`expenses`);
                  }}
                >
                  Expenses
                </p>
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </div>
      </div>
    </>
  );
};

export default AdminDash;
